@import url(shuffle-button-Leon.css);
@import url(pin.css);
@import url(search.css);

:root {
  --arrow-bg: rgba(255, 255, 255, 0.3);
  --arrow-icon: url(https://upload.wikimedia.org/wikipedia/commons/9/9d/Caret_down_font_awesome_whitevariation.svg);
  --option-bg: white;
  --select-bg: rgba(255, 255, 255, 0.2);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

select {
    appearance: none;
    border: 0;
    outline: 0;
    font: inherit;
    /* width: 20rem; */
    padding: 1rem 4rem 1rem 1rem;
    background: var(--arrow-icon) no-repeat right 0.8em center / 1.4em,
      linear-gradient(to left, var(--arrow-bg) 3em, var(--select-bg) 3em);
    color: white;
    border-radius: 0.25em;
    box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.2);
    cursor: pointer;
    /* Remove IE arrow */
    &::-ms-expand {
      display: none;
    }
    /* Remove focus outline */
    &:focus {
      outline: none;
    }
    /* <option> colors */
    option {
      color: inherit;
      background-color: var(--option-bg);
    }
  }
  

.mainContainer {
  display: flex;
  height: 100vh;
}

.leftBar {
  width: 20%;
  background: rgb(203, 200, 255);
  background: linear-gradient(
    0deg,
    rgba(203, 200, 255, 1) 0%,
    rgba(255, 117, 244, 1) 47%,
    rgba(0, 212, 255, 1) 100%
  );
  padding: 10px;
  height: 100%;
  display: flex;
  flex-direction: column;
}

input[type="text"] {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
}
input[type="text"]:focus,
#messageTxt:focus {
  outline: none;
  border-color: #6b6eff;
  box-shadow: 0 0 5px rgba(107, 110, 255, 0.5);
}

#messageTxt {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
}

#messageForm button,
#userForm button {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
  background-color: #4346fd;
  color: white;
  font-weight: bold;
}

#messageForm button:hover,
#userForm button:hover {
  background-color: #6b6eff;
  color: white;
  cursor: pointer;
}
.formDiv {
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.message {
  border: 3px solid #ccc;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  width: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background-color: white;
  position: absolute;
  word-wrap: break-word;
  min-width: none;
  max-width: 25%;
}

.message h4 {
  margin: 0;
  font-size: 1.2em;
  width: 100%;
}

.message p {
  margin: 5px 0;
  width: 100%;
}

.message span {
  font-size: 1.5em;
  margin-left: 10px;
}

.like {
  background-color: #4caf50; /* Grön för "like" */
  color: white;
}
.dislike {
  background-color: #f44336; /* Röd för "dislike" */
  color: white;
}

.liked {
  background-color: #45a049; /* Ljusgrön när gillat */
}

.disliked {
  background-color: #d32f2f; /* Ljusröd när ogillat */
}

#messagesContainer {
  width: 100%;
  overflow: scroll;
  height: 100%;
  position: relative;
  overflow: hidden;
  background-color: #f9f9f9;
  padding-left: 2%;
}

/* Alriks feature*/
.shadowBanned {
  display: none;
}

.removeButton {
  border: none;
  background-color: transparent;
}

.removeButton:hover {
  cursor: pointer;
}

#fireworksContainer {
  position: absolute;
  top: 0;
  left: 20%;
  width: 80%;
  height: 100%;
  z-index: 99999999999999;
}
.removeButton {
  border-radius: 10rem;
  aspect-ratio: 1/1;
  border: none;
  padding: 1px;
  margin-left: auto;
}

.contentContainer {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.sideMenu {
  background: linear-gradient(
    0deg,
    rgba(203, 200, 255, 1) 50%,
    rgba(0, 212, 255, 1) 100%
  );
  padding: 10px;
  height: 32vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 20px;
  border-radius: 10px;
  border: #6b6eff 2px solid;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  width: 110px;
  top: 280px;
}

.sideMenu ul {
  list-style: none;
  padding: 0;
  width: 100%;
}

.sideMenu li {
  margin: 15px 0;
  width: 100%;
}

.sideMenu a {
  text-decoration: none;
  font-size: 18px;
  font-weight: bold;
  color: white;
  padding: 10px;
  display: block;
  text-align: center;
  border-radius: 5px;
  transition: background 0.3s;
}

.sideMenu a:hover {
  background: rgba(255, 255, 255, 0.2);
}

.gradient-text {
  background: linear-gradient(
    0deg,
    rgba(203, 200, 255, 1) 0%,
    rgba(255, 117, 244, 1) 27%,
    rgba(0, 212, 255, 1) 100%
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  font-size: 4em; /* Justera efter behov */
  font-weight: bold;
}

.Header {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 20vh;
  width: 100%;
}

#contactForm {
  display: flex;
  flex-direction: column;
  width: 60%;
  max-width: 500px;
  margin: 55px;
  background: linear-gradient(
    135deg,
    rgba(203, 200, 255, 1) 0%,
    rgba(255, 117, 244, 1) 50%,
    rgba(0, 212, 255, 1) 100%
  );
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
}

#contactForm label {
  font-weight: bold;
  margin-top: 10px;
}

#contactForm input,
#contactForm textarea {
  width: 100%;
  padding: 10px;
  margin: 5px 0;
  border-radius: 5px;
  border: 1px solid #ccc;
}

#contactForm button {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border-radius: 5px;
  border: none;
  background-color: #4346fd;
  color: white;
  font-weight: bold;
  cursor: pointer;
}

#contactForm button:hover {
  background-color: #6b6eff;
}

.contactContainer {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  padding: 20px;
  background-color: #f9f9f9;
}

@keyframes shake {
  0% {
    transform: translateX(0);
  }
  10% {
    transform: translateX(-5px);
  }
  20% {
    transform: translateX(5px);
  }
  30% {
    transform: translateX(-5px);
  }
  40% {
    transform: translateX(5px);
  }
  50% {
    transform: translateX(-5px);
  }
  60% {
    transform: translateX(5px);
  }
  70% {
    transform: translateX(-5px);
  }
  80% {
    transform: translateX(5px);
  }
  90% {
    transform: translateX(-5px);
  }
  100% {
    transform: translateX(0);
  }
}

/* Apply the animation to your element */
.shake {
  animation: shake 1s;
}
/* Standard ljusläge (Light Mode) */
body {
  background-color: white;
  color: black;
  font-family: Arial, sans-serif;
  transition: background-color 0.3s, color 0.3s;
}

body.dark-mode {
  background-color: #121212;
  color: #ffffff;
}

#dark-mode-toggle {
  position: fixed;
  top: 10px;
  right: 10px;
  padding: 10px 15px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  background-color: #000;
  color: #fff;
  transition: background-color 0.3s, color 0.3s;
}

/* Ändra knappens stil i Dark Mode */
body.dark-mode #dark-mode-toggle {
  background-color: #fff;
  color: #000;
}

body.dark-mode .message {
  background-color: #333;
  color: white;
}

body.dark-mode input[type="text"],
body.dark-mode #messageTxt {
  background-color: #444;
  color: white;
  border: 1px solid #666;
}

body.dark-mode #messageForm button {
  background-color: #555;
}

body.dark-mode #messageForm button:hover {
  background-color: #6b6eff;
}

body.dark-mode #messagesContainer {
  background-color: #222;
}

@media (max-width: 768px) {
  .mainContainer {
    flex-direction: column;
    height: 100dvh;
  }

  .leftBar {
    width: 100%;
    height: auto;
    padding: 20px;
  }

  .sideMenu {
    position: static;
    width: 100%;
    margin: 0;
    height: auto;
    box-shadow: none;
  }

  .sideMenu ul {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
  }

  .sideMenu li {
    margin: 10px;
  }

  .contactContainer {
    width: 100%;
    padding: 10px;
  }

  #messagesContainer {
    position: relative;
    width: 100%;
    height: 100%;
    /* overflow: hidden; */
    background-color: #f9f9f9;
    padding: 10px;
  }

  .message {
    max-width: 90%;
    margin: 10px auto;
  }

  #contactForm {
    width: 90%;
    margin: 20px auto;
  }

  #dark-mode-toggle,
  #shuffleButton {
    padding: 8px 12px;
    font-size: 14px;
  }

  .gradient-text {
    font-size: 2em;
  }

  .Header {
    height: auto;
    padding: 10px;
  }
}
