  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

select {
  appearance: none;
  font: inherit;
  background: var(--arrow-icon) no-repeat right .8em center / 1.4em, linear-gradient(to left, var(--arrow-bg) 3em, var(--select-bg) 3em);
  color: #fff;
  cursor: pointer;
  border: 0;
  border-radius: .25em;
  outline: 0;
  padding: 1rem 4rem 1rem 1rem;
  box-shadow: 0 0 1em #0003;

  &::-ms-expand {
    display: none;
  }

  &:focus {
    outline: none;
  }

  & option {
    color: inherit;
    background-color: var(--option-bg);
  }
}

